<template>
  <div id="app">
    <div class="container">
      <h1 class="title">모두의 주차장 시스템</h1>
      <!-- 주차 공간 상태 표시 -->
      <div class="parking-status">
        <h2 class="subtitle">주차공간 상태</h2>
        <div class="parking-slot" v-for="(slot, index) in parkingSlots" :key="index">
          <div :class="['slot', slot ? 'filled' : 'empty']">
            <p class="slot-info">슬롯 {{index + 1}}: {{slot ? "차량 있음" : "빈 공간"}}</p>
            <button class="action-btn park-btn" v-if="!slot" @click="parkCar(index)">차량 입차</button>
            <button class="action-btn remove-btn" v-if="slot" @click="removeCar(index)">차량 출차</button>
          </div>
        </div>
      </div>
      <!-- 주차된 차량 수 표시 -->
      <div class="status-info">
        <h3>현재 주차된 차량 수: {{parkedCarCount}}/{{totalSlots}}</h3>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "App",
    data() {
      return {
        totalSlots: 5,
        parkingSlots: Array(5).fill(false)
      }
    },
    // 계산된 속성
    computed: {
      parkedCarCount() {
        return this.parkingSlots.filter(slot => slot).length;
      }
    },
    methods: {
      parkCar(index) {
        if (this.parkingSlots[index] === false) {
          this.parkingSlots[index] = true;
        } else {
          alert("이 공간에는 이미 차량이 주차되어 있습니다.");
        }
      },
      removeCar(index) {
        if (this.parkingSlots[index] === true) {
          this.parkingSlots[index] = false;
        } else {
          alert("이 공간은 이미 비어 있습니다.");
        }
      }
    }
  }
</script>

